.sidebar {
  background: linear-gradient(0deg, #0ea4c2 0%, #13cfb9 100%);
  height: 100%;
  width: 60px;
  position: fixed;
  img {
    transform: scale(2);
    cursor: pointer;
    width: 18px;
    height: 14px;
    margin-left: 19px;
    margin-top: 36px;
  }

  .page-icons {
    margin-top: 120px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 24px;
    gap: 32px;

    i {
      cursor: pointer;
    }
    .ui {
      transform: scale(1.6);
    }

    .actived::after {
      content: "";
      height: 36px;
      width: 2.5px;
      background-color: white;
      position: absolute;
      left: 1%;
    }
  }

  .logout {
    position: absolute;
    bottom: 5%;
    cursor: pointer;
    color: white;
    left: 50%;
    transform: translateX(-50%) scale(1.5);
  }
}
